	module.exports = {"data":{"markdownRemark":{"html":"<p>Complete the article on political supply.</p>\n<p>After the government and political system gradually withdrew from economic management, the government's functions were increasingly concentrated on a macro and indirect management. In this way, the whole society is more like a big system. In this system, there is no power to control everything, and there is no power to intervene through a series of channels. All subsystems are in one interaction. The principle of a market economy may be like this. After the political system loses its past powers and channels, how to control the economic development of society must have new methods. This new approach, from a systemic point of view, is to increase the supply of politics, that is, input, in a large system. Hope for ideal output through political input. Therefore, the political supply will be a major method for the government and the political system to regulate the entire socio-economic development under the conditions of a market economy.</p>\n<p>Class. The first is the study of administrative management and the discussion of the Analects of Confucius. The second is modern Western political thoughts. Discussing \"Zarathustra is like saying\", the Commercial Press translated the title into \"Suluzhi Quotations\". I don't know how such a big difference.</p>\n<p>Young people do not understand the thoughts of Confucius. I don't know that Confucian thought has its reasons for existence in a cultural atmosphere like China. It is not that human power can be eliminated. It is not only an ideology but also a reflection of a way of life. In Chinese society, people have always emphasized the coordination of people's inner world from the outside world, not from the outside world. All the order, the political ones, the economic ones, the social ones, and finally the spiritual order. Before the formation of a spiritual order, any external order is not strong. This is the most valuable part of Confucianism. A world cannot be without</p>\n<p>Order can not be without ethical order, Confucianism emphasizes this point. The problem is not what kind of content is used to build order, but the order itself. Content always develops with the development of history.</p>\n<p>One of the classmates discussed Nietzsche and said that Nietzsche first paid attention to the inner world of people, paid attention to the meaning of human life, and paid attention to the obliteration of human nature by modern science and technology. Later Freud, Sartre and Marcuse developed Nietzsche's worries in these three aspects. In essence, Nietzsche wants to volatize human nature and human desires. He believes that modern civilization constrains human life instinct, so there must be \"superman.\" Nietzsche himself thought that he was a \"superman\", but in the end he probably thought too much and got schizophrenia.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0046.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0046.md","frontmatter":{"title":"March 1st Tuesday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0046.md absPath of file >>> MarkdownRemark"}}

